'use client';
import { useEffect, useState } from 'react';

type Speaker = {
  id: number;
  name: string;
  bio: string;
  photo: string; // Full URL already provided by Django
};

export default function SpeakersPage() {
  const [speakers, setSpeakers] = useState<Speaker[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetch('http://127.0.0.1:8000/api/speakers/')
      .then((res) => res.json())
      .then((data) => {
        setSpeakers(data);
        setLoading(false);
      })
      .catch((err) => {
        setError('Failed to load speakers');
        setLoading(false);
      });
  }, []);

  if (loading) return <p className="p-8">Loading speakers...</p>;
  if (error) return <p className="p-8 text-red-500">{error}</p>;

  return (
    <div className="min-h-screen p-8 sm:p-16 bg-white">
      <h1 className="text-3xl font-bold mb-8 text-center">Meet the Speakers</h1>
      <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
        {speakers.map((speaker) => (
          <div key={speaker.id} className="border rounded-lg p-4 shadow-sm">
            {speaker.photo && (
              <img
                src={speaker.photo}
                alt={speaker.name}
                className="w-full h-48 object-cover rounded mb-4"
              />
            )}
            <h2 className="text-xl font-semibold">{speaker.name}</h2>
            <p className="text-gray-600 mt-2">{speaker.bio}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
